.App {
  text-align: center;
  
  
}

body {
  background-image: url(Assets/brooklynsbooth.jpeg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}









.card {
  
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: whitesmoke;
  width: 30%;
  margin-left: auto;
  margin-right: auto;
  top: 1; left: 0; bottom: 0; right: 0;
  position: absolute;
  bottom: 0;
  border-radius: 2%;
  gap: 5px;
  padding-top: 5px;
  opacity: 80%;
  border: 2px solid;
  border-color: silver;
 
}



  

  
  


  
  
  




